/* App.css */

body, html {
  margin: 0;
  padding: 0;
  font-family: 'Arial', sans-serif;
  background: #3a0ca3; /* Purple background */
  color: white;
  overflow-x: hidden;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  text-align: center;
  padding: 20px;
}

.title {
  font-size: 2rem;
  margin-bottom: 20px;
}

.countdown {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
  margin-bottom: 20px;
}

.time-box {
  background: rgba(255, 255, 255, 0.1);
  padding: 10px 15px;
  border-radius: 10px;
  min-width: 60px;
}

.sub-text {
  font-size: 1rem;
}

.birthday-message {
  font-size: 1.5rem;
}

/* Balloons */
.balloons {
  display: flex;
  justify-content: center;
  margin-top: 20px;
  gap: 10px;
}

.balloon {
  width: 30px;
  height: 40px;
  border-radius: 50%;
}

/* Balloon colors */
.violet { background-color: violet; }
.pink { background-color: pink; }
.lavender { background-color: lavender; }
.purple { background-color: purple; }

/* Flying kisses */
.kisses {
  position: fixed;
  bottom: 0;
  width: 100%;
  pointer-events: none;
}

.kiss {
  position: absolute;
  font-size: 2rem;
}

/* Responsive */
@media (max-width: 600px) {
  .title {
    font-size: 1.5rem;
  }
  .time-box {
    min-width: 50px;
    padding: 8px 10px;
  }
  .sub-text {
    font-size: 0.9rem;
  }
  .birthday-message {
    font-size: 1.2rem;
  }
  .balloon {
    width: 25px;
    height: 35px;
  }
  .kiss {
    font-size: 1.5rem;
  }
}
